import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { BudgetWidgetModule } from '@backbase/budget-widget-ang';
import { BackbaseCoreModule } from '@backbase/foundation-ang/core';
import { HeaderModule } from '@backbase/ui-ang/header';
import { BudgetJourneyComponent } from './budget-journey.component';
import { BudgetViewComponent } from './views/budgets-view.component';

export const defaultRoute: Route = {
  path: '',
  component: BudgetJourneyComponent,
  children: [
    {
      path: '',
      component: BudgetViewComponent,
    },
  ],
};

@NgModule({
  declarations: [BudgetJourneyComponent, BudgetViewComponent],
  imports: [
    CommonModule,
    BackbaseCoreModule.withConfig({
      classMap: { BudgetJourneyComponent },
    }),
    RouterModule,
    HeaderModule,
    BudgetWidgetModule,
  ],
})
export class BudgetJourneyModule {
  static forRoot(
    data: { route: Route; [key: string]: any } = { route: defaultRoute },
  ): ModuleWithProviders<BudgetJourneyModule> {
    return {
      ngModule: BudgetJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
