import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';

export interface BudgetJourneyConfiguration {
  /**
   * Show budget values as percentages
   */
  showPercentage: boolean;
  /**
   * Set budget safe limit value
   */
  budgetSafeZoneLimit: number;
  /**
   * Notification dismiss time
   */
  notificationDismissTime: number;
  /**
   * Maximum number of budgets shown
   */
  maxBudgets: number | null;
}

// eslint-disable-next-line
export const BudgetJourneyConfigurationToken = new InjectionToken<BudgetJourneyConfiguration>(
  'BudgetJourneyConfiguration injection token',
);

const configDefaults: BudgetJourneyConfiguration = {
  showPercentage: false,
  budgetSafeZoneLimit: 80,
  notificationDismissTime: 5,
  maxBudgets: null,
};

@Injectable()
export class BudgetJourneyService {
  private _config: BudgetJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(BudgetJourneyConfigurationToken)
    config: BudgetJourneyConfiguration,
  ) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
  }

  get defaults(): BudgetJourneyConfiguration {
    return configDefaults;
  }

  get showPercentage(): boolean {
    return this._config.showPercentage;
  }

  get budgetSafeZoneLimit(): number {
    return this._config.budgetSafeZoneLimit;
  }

  get notificationDismissTime(): number {
    return this._config.notificationDismissTime;
  }

  get maxBudgets(): number | null {
    return this._config.maxBudgets;
  }
}
